.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App .title p{
  font-size: 70px;
  margin: auto;
  margin-top: -130px;
  margin-right: 400px;
  text-align: right;
  color: lightgray;
}

.App .title img {
  margin: auto;
  margin-left: -910px;
  margin-bottom: 12px;
  width: 10%;
  height: 10%;
}

.App .subtitle h3 {
  margin-top: -17px;
  margin-bottom: 100px;
  margin-left: 420px;
  text-align: left;
  font-size: 20.5px;
  color: darkgray;
}

.App .card {
  float: left;
  padding: 0px;
  margin-left: 220px;
  border-color:#383838;

}

.App-intro {
  font-size: large;
}

.flippy-container {
  padding-bottom: 20px;
}

@keyframes App-logo-spin {
  from { transform: rotate(0deg); }
  to { transform: rotate(360deg); }
}

.App .footer {
  text-align: center;
  margin: auto;
  margin-top: 1350px;

}

.footer .playarrow {
   font-size: 3em;
   color: darkgray;
}

.footer .leftarrow {
  margin-left: -140px;
  margin-top: -70px;
  font-size: 3em;
  color: darkgray;
}

.footer .leftarrow .Link {
  color: darkgray;
}

.footer .leftarrow .Link:hover {
  color: white;
  font-size: .9em;
}

.footer .rightarrow {
  margin-top: -70px;
  margin-left: 140px;
  font-size: 3em;
  color: darkgray;
}

.rightarrow .Link {
  color: darkgray;
}

.rightarrow .Link:hover {
  color: white;
  font-size: .9em;
}