.container .title img {
    margin: auto;
    height: 50%;
    width: 50%;
  }

.container img {
  margin: auto;
  margin-top: 100px;
  display: list-item;
  width: 10%;
  height: 10%;
}

.container .body h2 {
  text-align: center;
  margin: auto;
  font-size: 40px;
  color: lightgray;
  margin-top: 100px;
}

.body {
  background-color: #383838;
  margin-top: -200px;
}

.body .email{
    margin-left: 35px;
    font-size: 100px;
    color: darkgray;
}

.body .email:hover{
    color: lightgray;
}

.body .email p{
    font-size: 30px;
    color: darkgray;
    visibility: hidden;
}

.body .email:hover p{
    color: white;
    bottom:30px;
    left:0px;
    visibility: visible;
}

.body .linkedin{
    font-size: 100px;
    color: darkgray;
}

.body .linkedin:hover{
    color: lightgray;
}

.body .github{
  margin: auto;
    font-size: 100px;
    text-decoration: none;
    color: darkgray;
}

.body .github:hover{
    color: lightgray;
}

.container .playarrow {
    text-align: center;
    margin-top: -200px;
    font-size: 3em;
    color: darkgray;
 }
 
 .container .leftarrow {
   text-align: center;
   margin-left: -150px;
   margin-top: -72px;
   font-size: 3em;
   color: darkgray;
 }
 
 .container .leftarrow .Link {
   color: darkgray;
 }
 
 .container .leftarrow .Link:hover {
   color: white;
   font-size: .9em;
 }
 
 .container .rightarrow {
  text-align: center;
  margin-left: 150px;
  margin-top: -72px;
  font-size: 3em;
  color: darkgray;
 }
 
 .container .rightarrow .Link {
   color: darkgray;
 }
 
 .container .rightarrow .Link:hover {
   color: white;
   font-size: .9em;
 }